.steps-container {
  position: relative;
  max-width: 600px;
  width: 75%;
  margin: 20px auto 100px;
  height: 2px;
  background: black;
  background: -moz-linear-gradient(left, black 0%, rgba(0, 0, 0, 0.26) 54%, rgba(0, 0, 0, 0.19) 100%);
  background: -webkit-linear-gradient(left, black 0%, rgba(0, 0, 0, 0.26) 54%, rgba(0, 0, 0, 0.19) 100%);
  background: linear-gradient(to right, black 0%, rgba(0, 0, 0, 0.26) 54%, rgba(0, 0, 0, 0.19) 100%); }
  .steps-container.done {
    background: black; }
  .steps-container .step {
    padding-top: 20px;
    position: absolute; }
    .steps-container .step:after {
      position: absolute;
      content: '';
      width: 20px;
      height: 20px;
      background: #fff;
      border: 8px solid #dedede;
      left: 50%;
      top: -10px;
      transform: translate(-50%, 0);
      border-radius: 50%; }
    .steps-container .step.current:after {
      border: 8px solid #066977;
      width: 24px;
      height: 24px; }
  .steps-container .step-1 {
    left: 0;
    transform: translate(-50%, 0); }
  .steps-container .step-2 {
    left: 50%;
    transform: translate(-50%, 0); }
  .steps-container .step-3 {
    right: 0;
    transform: translate(50%, 0); }

